import { Suspense, lazy } from 'react';
import { Navigate, useRoutes, useLocation } from 'react-router-dom';
// layouts
import Layout from '../layout';
import LogoOnlyLayout from '../layout/LogoOnlyLayout';
// components

import LoadingScreen from '../components/LoadingScreen';

// guards
import GuestGuard from '../guards/GuestGuard';
import AuthGuard from '../guards/AuthGuard';

// ----------------------------------------------------------------------

const Loadable = (Component) => (props) => {
  // const { pathname } = useLocation();

  return (
    <Suspense fallback={<LoadingScreen />}>
      <Component {...props} />
    </Suspense>
  );
};

export default function Router() {
  return useRoutes([
    {
      path: 'auth',
      children: [
        {
          path: 'login',
          element: (
            <GuestGuard>
              <Login />
            </GuestGuard>
          ),
        },
        { path: 'verify', element:(<GuestGuard> <VerifyCode /></GuestGuard>) },
      ],
    },
    {
      path: '',
      children: [
        { element:(<AuthGuard>  <Home /> </AuthGuard>), index:true},
        { path: 'home', element:(<AuthGuard>  <Home /> </AuthGuard>)},
      ],
    },
    {
      path: 'admin',
      element:
        <Layout />
      ,
      children: [
        { path: 'device/:id', element: <DeviceEdit /> },
        { path: 'device-manage', element: <DeviceManage /> },
        { path: 'user-manage', element: <UserManage /> },
      ],
    },


    {
      path: '*',
      element: <LogoOnlyLayout />,
      children: [
        { path: '404', element: <NotFound /> },
        { path: '*', element: <Navigate to="/404" replace /> },
      ],
    },
    { path: '*', element: <Navigate to="/404" replace /> },
  ]);
}

// Authentication
const Login = Loadable(lazy(() => import('../pages/auth/Login')));
const VerifyCode = Loadable(lazy(() => import('../pages/auth/VerifyCode')));

const Home = Loadable(lazy(() => import('../pages/Home')));
const DeviceManage = Loadable(lazy(() => import('../pages/admin/DeviceManage')));
const DeviceEdit = Loadable(lazy(()=>import('../pages/admin/DeviceEdit')));
const UserManage = Loadable(lazy(() => import('../pages/admin/UserManage')));
const NotFound = Loadable(lazy(() => import('../pages/Page404')));
