import { createContext, useEffect, useReducer } from 'react';
import PropTypes from 'prop-types';

import {
    getAuth,
    signOut,
    onAuthStateChanged,
    signInWithPhoneNumber,
    RecaptchaVerifier
} from 'firebase/auth';



import { initializeApp } from 'firebase/app';
import { FIREBASE_API } from '../config';

// utils
import axios from '../utils/axios';
import { isValidToken, setSession } from '../utils/jwt';


// const firebaseApp = initializeApp(FIREBASE_API);

// const AUTH = getAuth(firebaseApp);

// AUTH.settings.appVerificationDisabledForTesting  = true; 
// ----------------------------------------------------------------------

const initialState = {
    isAuthenticated: false,
    isInitialized: false,
    user: null,
};

const handlers = {
    INITIALIZE: (state, action) => {
        const { isAuthenticated, user } = action.payload;
        return {
            ...state,
            isAuthenticated,
            isInitialized: true,
            user,
        };
    },

    OTPFINAL: (state, action) => {
        const { final } = action.payload;
        return {
            ...state,
            isAuthenticated: false,
            isInitialized: true,
            final,
            user: null,
        }
    },
    LOGINED: (state, action) => {
        const { user } = action.payload;

        return {
            ...state,
            isAuthenticated: true,
            user,
        };
    },
    LOGOUT: (state) => ({
        ...state,
        isAuthenticated: false,
        final: null,

        user: null,
    }),


};

const reducer = (state, action) => (handlers[action.type] ? handlers[action.type](state, action) : state);

const AuthContext = createContext({
    ...initialState,
    method: 'jwt',
    login: () => Promise.resolve(),
    logout: () => Promise.resolve(),
});

// ----------------------------------------------------------------------

AuthProvider.propTypes = {
    children: PropTypes.node,
};

function AuthProvider({ children }) {
    const [state, dispatch] = useReducer(reducer, initialState);

    useEffect(() => {

        const initialize = async() => {
            try {
                const accessToken = window.localStorage.getItem('accessToken');
                
                if (accessToken && isValidToken(accessToken)) {
                    setSession(accessToken);

                    const response = await axios.get('/api/auth/my-account');
                    const { user } = response.data;

                    dispatch({
                        type: 'INITIALIZE',
                        payload: {
                            isAuthenticated: true,
                            user,
                        },
                    });
                } else {
                    dispatch({
                        type: 'INITIALIZE',
                        payload: {
                            isAuthenticated: false,
                            user: null,
                        },
                    });
                }
            } catch (err) {
                console.error(err);
                dispatch({
                    type: 'INITIALIZE',
                    payload: {
                        isAuthenticated: false,
                        user: null,
                    },
                });
            }
        };

        initialize();
    }, []);

    const otpVerify = async(otp, callback) => {
        try {
            const phoneNumber = window.phoneNumber;
            const otpResult = await axios.post('/api/auth/verifyOtp', {
                phoneNumber,
                otp
            });
            const { success } = otpResult.data;
            if (success) {
                // register or update mongodb
                const response = await axios.post('/api/auth/register', {
                    phoneNumber,
                });
                if (response.status === 200) {
                    const { token, user } = response.data;
                    setSession(token);
                    dispatch({
                        type: 'LOGINED',
                        payload: {
                            user,
                        },
                    });
                    callback({ success: true });

                }
            } else {
                callback({ success: false, err: 'unmathed otpcode' });
            }

        } catch (err) {

            
            callback({ success: false, err: 'otp response err' });
        }
    }
    const login = async(phoneNumber) => {

        const response = await axios.post('/api/auth/login', {
            phoneNumber
        });

        const { token, user } = response.data;

        // verify phoneNumber via firebase
        if (!token) {
            try {
                window.phoneNumber = phoneNumber;
                return true;

            } catch (err) {
               
                return false;
            }
        }
        setSession(token);
        dispatch({
            type: 'LOGINED',
            payload: {
                user,
            },
        });
        return true;
    };


    const logout = async() => {
        try {
            setSession(null);

            dispatch({ type: 'LOGOUT' });
            // signOut(AUTH);
        } catch (err) {

            console.log(err);
        }

    };

    return ( <AuthContext.Provider value = {
            {
                ...state,
                method: 'jwt',
                    login,
                    logout,
                    otpVerify,
            }
        } > { children } </AuthContext.Provider>
    );
}

export { AuthContext, AuthProvider };