import * as Yup from 'yup';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { Stack, Alert } from '@mui/material';
import { LoadingButton } from '@mui/lab';

import { useNavigate } from 'react-router-dom';
// hooks
import useAuth from '../../hooks/useAuth';
import useIsMountedRef from '../../hooks/useIsMountedRef';
// components
import { FormProvider, RHFTextField } from '../../components/hook-form';

// ----------------------------------------------------------------------

export default function LoginForm() {
  const { login } = useAuth();
  const navigate = useNavigate();

  const isMountedRef = useIsMountedRef();
  const LoginSchema = Yup.object().shape({
    phoneNumber: Yup.string().required('Phone Number is required'),
  });

  const defaultValues = {
    phoneNumber: '',
    remember: true,
  };

  const methods = useForm({
    resolver: yupResolver(LoginSchema),
    defaultValues,
  });

  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      const next = await login(data.phoneNumber);
      if (next) {
        navigate('/auth/verify');
      }
    } catch (error) {
      console.error(error, 'is erro');
      reset();
      if (isMountedRef.current) {
        setError('afterSubmit', 'verification server error');
      }
    }
  };

  return (
    <>
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
        <Stack spacing={3}>
          {' '}
          {!!errors.afterSubmit && <Alert severity="error"> {errors.afterSubmit.message} </Alert>}{' '}
          <RHFTextField name="phoneNumber" label="phoneNumber" />
          <LoadingButton
            fullWidth
            size="large"
            sx={{ bgcolor: 'grey.50016', border: '1px solid', borderColor: 'grey.50048' }}
            type="submit"
            variant="contained"
            loading={isSubmitting}
          >
            Нэвтрэх{' '}
          </LoadingButton>{' '}
        </Stack>
      </FormProvider>{' '}
      <div id="recaptcha-container" />
    </>
  );
}
